.mainbutton {
  width: 149px;
  height: 56px;
  background-color: #ff0000;
  border-radius: 8px;
  font-family: "DINComp";
  outline: none;
  border: none;
  color: white;
  font-size: 16px;
  line-height: 24px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.mainbutton.black:hover {
  color: black;
  outline: 3px solid black;
  transition: all 0.3s ease-out;
}
.mainbutton.white:hover {
  color: white;
  outline: 3px solid white;
  transition: all 0.3s ease-out;
}
